import React from 'react';
import ReactDOM from 'react-dom';
import Game from './Game';
import './Start.css';

class Start extends React.Component {
  constructor(props) {
  	super(props);
  }

  play1player() {
  	ReactDOM.render(<Game players = {1}/>, document.getElementById('game'));
  }

  play2player() {
  	ReactDOM.render(<Game players = {2}/>, document.getElementById('game'));
  }

  render() {
  	var count = 0;
    var columns = this.props.cols;
  	
    return (
    	<div className="Startmenu">
      	<div className ="Menu">
        	<h1 className="Title"> Connect Four </h1>
        	<button className = "Playerbutton" onClick={this.play1player}> VS CPU </button>
        	<button className = "Playerbutton" onClick={this.play2player}> VS FRIEND </button>
      	</div>
      </div>
    );
  }
}

export default Start;